body {
  --main-color: #00acdc;
  margin: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.navigation {
  text-transform: uppercase;
  flex-wrap: nowrap;
  align-items: center;
  margin: 0;
  padding-inline-start: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.menu__list {
  margin: 0 0 0 20px;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.link {
  box-sizing: border-box;
  color: #000;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.logo__img {
  border: 0;
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  display: flex;
}

li:last-child {
  margin-right: 0;
}

.is-active:after {
  background-color: var(--main-color);
  content: "";
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.link:hover, .is-active {
  color: var(--main-color);
}

